import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'goodsOne',
	data() {
		return {
			arr:[],
		};
	},
	props: {
		msgDatas: {
			type: Object
		}
	},
	computed: {
		i18n () {
		  return this.$t('listModules')  
		},
	},
	created() {
		this.arr.push(this.msgDatas)
		console.log(this.arr)
	},
	mounted() {},
	methods: {
		...mapMutations({
		}),
		gotocloud(e){
			console.log('to慧聪云详情')
			uni.navigateTo({
				url:'/modules/find/findlist/find-cloud?id='+e
			})
		},
	}
};
